import React,{useState} from 'react';
import MainPage from '../components/MainPage';

import { apiget } from '../Api/Api';

import ShowGrid from '../components/Show/ShowGrid';
import ActorGrid from '../components/Actor/ActorGrid';
import { useLastQuery } from '../Api/CustomHook';

import {
    SearchInput,
    RadioInputsWrapper,
    SearchButtonWrapper,
  } from './HomeStyled';

const Home=()=> {
    console.log(useLastQuery());
    const [value, setValue] = useLastQuery();
    
    const [result,setResult] = useState(null);

    const [radioButton,setradioButton] = useState('');

    const renderInput=(event)=>{
        setValue(event.target.value);
    };

    const onKeyDown=(event)=>{

       if(event.keyCode === 13){
           search();
       }
    };

    const search=()=>{
        apiget(`/search/${radioButton}?q=${value}`).then(re=>
            setResult(re) );  
    } ;

    const catagoryChange=(event)=>{
        setradioButton(event.target.value);
    }
    const results=()=>{
        if (result && result.length === 0){
            return <div>No result found at the moment</div>;
        }
        if( result && result.length>0){
            return result[0].show 
            ? <ShowGrid data={result}/>:
            <ActorGrid data={result}/>
        }
    }
    // console.log(result);
    return (
       <MainPage>
          <SearchInput type="text" value={value} onChange={renderInput} onKeyDown={onKeyDown}/>
          <RadioInputsWrapper>
                <input type="radio" id="show" name="catagory" 
                value="shows" onChange={catagoryChange}/>

                    <label htmlFor="show">Shows</label>

                <input type="radio" id="people" name="catagory" 
                value="people" onChange={catagoryChange}/>

                    <label htmlFor="people">Actors</label>
          </RadioInputsWrapper>
          <SearchButtonWrapper>

          <button type="button" onClick={search}>Search</button>
          </SearchButtonWrapper>
          {results()}
       </MainPage>
    )
}

export default Home
