import React ,{useState,useEffect} from 'react'
import MainPage from '../components/MainPage'
import {useShows} from '../Api/CustomHook'
import { apiget } from '../Api/Api'; 
import ShowGrid from '../components/Show/ShowGrid';
const Favorite=()=> {

    const [favorite]=useShows();
    const [show,setShow]=useState(null);
    const[err,setErr]=useState(null);
    const [isLoading,setisLoading]=useState(true);

    useEffect(()=>{
        if (favorite && favorite.length > 0) {
            const promises = favorite.map(showId => apiget(`/shows/${showId}`));
            console.log(promises);
            Promise.all(promises)
              .then(apiData => apiData.map(show => ({ show })))
              .then(results => {
                setShow(results);
                setisLoading(false);  
              })
              .catch(err => {
                setErr(err.message);
                setisLoading(false);
              });
          } else {
            setisLoading(false);
          }
        }, [favorite]);
    return (
       <MainPage>
           {isLoading && <div>Loading.......</div>}

           {!isLoading && !show && <div>No favorite list are found</div>}

           {err && <div>Error oocured: {err}</div>}

           {!isLoading && !err && show && <ShowGrid data={show}/> }
       </MainPage>
    )
}

export default Favorite;
