import React from 'react';
import {useParams} from 'react-router-dom';
import  Image from '../img/not-found.png';
import ShowDetails from '../components/Show/ShowDetails';
import { InfoBlock, ShowPageWrapper } from '../components/Styled';
import { useShow } from '../Api/CustomHook';



const ShowPage=()=> {
    const { id } = useParams();
    const {show, isLoading, error}= useShow(id);
    if (isLoading) {
        return <div>Data is being loaded</div>;
    }
    
      if (error) {
        return <div>Error occured: {error}</div>;
      }
   console.log(show);       
    return (
        <ShowPageWrapper>
            <InfoBlock>
                <ShowDetails
                    image={show.image ? show.image.medium : Image}
                    name={show.name}
                    language={show.language}
                    genres={show.genres}
                    summary={show.summary}
                    rating={show.rating}
                    status={show.status}
                    network={show.network}
                    premiered={show.premiered}
                    seasons={show._embedded.seasons}
                    cast={show._embedded.cast}
                />
            </InfoBlock>
        </ShowPageWrapper>
    )
}

export default ShowPage;
