import React from 'react'
import  Image from '../../img/not-found.png'
import {Details, Head, Season, TagList , Cast} from './ShowStyled'
const ShowDetails = ({name,image,rating,summary,genres,cast,seasons,status,network,premiered,language}) => {

    return (
        <div>
            <Head>
                <div className="img-border">
                    <img src={image} alt="show"/>
                </div>
                <div className="textside">
                    <h1> 
                        {name} 
                    </h1>
                    <p>{language}</p>
                    <p><span>★</span>{rating.average || 'N/A'}</p>
                    <div> 
                        Tags : {'   '}
                     <TagList  className="Tags">{genres.map((item,index)=>{
                            return (
                               <span key={index}>{item} </span>
                            );
                        }
                        )}</TagList>
                    </div>
                    
                    
                </div>
            </Head>

            <Details>
              
              <div dangerouslySetInnerHTML={{ __html: summary}}/>
              <p>Status : <b>{status}</b></p>
              <p>
                  Premiered : {premiered} <span>{network ? `on ${network.name}`: null}</span>
              </p>

            </Details>
            <h1>Seasons</h1>

            <Season>
                <p>Number of Season : <span>{seasons.length}</span></p>
               <p> Number of Episode Total : {' '}
                <span>{
                    seasons.reduce((acc,season)=> acc+season.episodeOrder,0)
                }</span></p>
                
                <div className="seasons">
                    {seasons.map((season)=>{
                        return <div key={season.id}>
                            <p>Season : <b>{season.number}</b></p>
                            <p>Episods : <b>{season.episodeOrder}</b></p>
                            <p>Date : <b>{season.premiereDate} - {season.endDate}</b></p>
                        </div>
                    })}
                </div>
                
            </Season>
            <h1>Cast</h1>

            <Cast>
               {cast.map((item, key) => (
                <div key={key} className="cast-item">
                    <div className="pic">
                        <img
                        src={item.person.image ? item.person.image.medium : Image}
                        alt="cast-person"
                        />
                    </div>
                    <div className="actorname">
                        <span>
                        <b>{item.person.name}</b> | {item.character.name}  {item.voice ? 'voice' :''}
                        </span>
                    </div>
                </div>
                ))}
            </Cast>
        </div>
    )
}
export default ShowDetails;
