import styled from "styled-components";
import {Card} from "../Styled";

export const ShowStyle = styled(Card)`
  .btns{
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    a {
      text-decoration-color: #000;
      color: #000;
      &:hover {
        text-decoration-color: blue;
        color: blue;
      }
    }
    button {
      outline: none;
      border: 1px solid #8e8e8e;
      border-radius: 15px;
      padding: 5px 20px;
      background-color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        cursor: pointer;
        background-color: #8e8e8e;
        color: white;

      }
    }
  }
`
//Show page styles
 export const Head=styled.div`
  display:flex; 
  margin-bottom: 20px;
  .img-border{
      border-radius: 35px;
      border: 1px solid black;
      min-width: 200px;
      width:250px;
      height: 320px;
      max-height: 380px;
      overflow: hidden;
      img{
          background-size: cover;
          width: 100%;
          height: 100%;
      }
  }
  .textside {
    margin-left: 30px;
  }
  p{
    font-weight: bold;
  }
  span{
    color: yellow;
  }
  @media only screen and (max-width: 768px) {
    flex-wrap: wrap;
    img {
      margin-bottom: 20px;
      margin: auto;
    }
    .textside {
      margin-left: 0;
      margin-top: 20px;
    }
  }
 `
 export const Star=styled.div`
   color: ${props=> (props.active ? 'yellow' : '')};
 `


 export const TagList=styled.div`
 display: inline-flex;
 flex-wrap: wrap;
  span{
    margin-right: 10px;
    background-color:  deepskyblue;
    border-radius: 10px;
    padding: 5px 10px;
    color: white;
  }
 `
 export const Details=styled.div`
  margin-bottom: 30px;
  span{
    font-weight: bold;
 }
  div{
    text-align: left;
    line-height: 1.5;
    color: #5f5f5f;
    font-style: italic;

  }
 `
 export const Season=styled.div`
  display: flex;
  flex-direction: column;
  span{
    font-weight: bold;
  }
  .seasons{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 20px;
    div{
      flex: 0 0 30%;
      background-color: whitesmoke;
      margin: 20px 50px;
      /* border-radius: 20px; */
      padding: 20px 20px;
      border-left: 2px solid black;
      @media only screen and (max-width: 768px) {
      flex: 1 0 100%;
    }
    }
  }
  
 `
 export const Cast=styled.div`
 display: flex;
 flex-wrap: wrap;
 justify-content: center; 
 margin-top: 20px;
 align-items: center;
  .cast-item{
    flex: 0 0 25%;
    display: flex;
    margin: 20px;
    align-items: center;
    background-color: whitesmoke;
    border-radius: 20px;
    padding: 10px 20px;
    @media only screen and (max-width: 768px) {
      flex: 1 0 100%;
    }
    .pic{
      width: 50px;
      min-width: 50px;
      height: 50px;
      border-radius: 25px;
      overflow: hidden;
      img{
        background-size: cover;
        width: 100%;
        height: 100%;
      }
    }
    .actorname{
      margin-left: 20px;
    }
  }
 `