import React from 'react'
import Shows from './Shows'
import  Image from '../../img/not-found.png'
import {Grid} from '../Styled'
import { useShows } from '../../Api/CustomHook'
function ShowGrid({data}) {

 const [starredshow,dispatchStarredshow]=useShows();

    return (
        <Grid>
            
            {data.map((item)=>{

            const isStarred = starredshow.includes(item.show.id);
            const starme=()=>{
                if(isStarred){
                    dispatchStarredshow({type: 'REMOVE' , showId:item.show.id});
                }else{
                    dispatchStarredshow({type: 'ADD',showId:item.show.id});
                }
            }
           return <Shows
                    key={item.show.id}
                    id={item.show.id}
                    name={item.show.name}
                    summary={item.show.summary}
                    image={item.show.image ? item.show.image.medium : Image}
                    onclick={starme}
                    isStarred={isStarred}
            />
        })}
        </Grid>
    )
}

export default ShowGrid
