import React from 'react';

import { useLocation } from 'react-router-dom';
import {NavList,LinkStyled} from './NavStyled'

const Nav=()=> {
    const location = useLocation();

    const links=[
       {to: '/' ,text: 'Home'},
       {to: '/Favorite', text: 'Favorite'}];
    return (
        <div>
      <NavList>
        {links.map(item => (
          <li key={item.to}>
            <LinkStyled
              to={item.to}
              className={item.to === location.pathname ? 'active' : ''}
            >
              {item.text}
            </LinkStyled>
          </li>
        ))}
      </NavList>
    </div>
    )
}

export default Nav;
